@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap');

.renewal {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 88px;

    .renewal-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .logo {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: clamp(24px, 5vw, 36px);
            width: clamp(128px, 23vw, 216px);
            margin-bottom: clamp(48px, 6.25vw, 60px);
            margin-left: 16px;

            img {
                width: 100%;
            }
        }

        .content-container {
            margin-bottom: clamp(18px, 4vw, 32px);
            padding: 0 24px;
            display: flex;
            flex-direction: column;
            align-items: center;

            h3 {
                font-size: clamp(21px, 4vw, 28px);
                margin-bottom: clamp(12px, 2.5vw, 14px);
                font-family: 'IBM Plex Sans', sans-serif;
                line-height: 150%;
                color: #E4E4E4;
                text-align: center;
            }

            p {
                width: clamp(266px, 70vw, 504px);
                font-size: clamp(10px, 2vw, 14px);
                font-family: 'IBM Plex Mono', monospace;
                color: #D6D6D6;
                line-height: 150%;
                text-align: center !important;
            }
        }

        .button {
            a {
                font-size: clamp(12px, 2.5vw, 18px);
                color: #FFFFFF;
                transition: 0.3s ease;
                text-decoration: underline;

                &:hover {
                    color: #FFFFFF;
                }
            }
        }
    }

    .email {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        margin-top: 24px;
        
        a {
            color: #FFFFFF;
            font-size: clamp(12px, 2vw, 18px);
            font-family: 'IBM Plex Mono', monospace;
            text-decoration: underline;
            transition: 0.3s ease;

            &:hover {
                color: #8A8A8A;
            }
        }
    }

    .background {
        position: absolute;
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        z-index: -1;

        .overlay {
            width: 100%;
            height: 100vh;
            background : rgba(0, 0, 0, 0.2);
        }

        img {
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            width: 100%;
            height: 100vh;
            object-fit: cover;
        }
    }
}